<template>
  <div class="products-page">
    <h2>Список продуктов</h2>
    <button
      @click="createNewproduct()"
      type="button"
      class="btn btn-outline-primary mb-4"
      v-if="start !== 'true'"
    >
      Добавить
    </button>
    <button
      @click="startShopping()"
      type="button"
      class="btn btn-outline-info mb-4 ml-2"
      v-if="products.length > 0 && action !== 'true'"
    >
      Начать покупки!
    </button>
    <button
      @click="endShopping()"
      type="button"
      class="btn btn-outline-primary mb-4 ml-2"
      v-if="action == 'true'"
    >
      Завершить
    </button>

    <div class="wrapper">
      <div
        class="product-wrapper"
        v-for="(product, i) in shoppingList"
        :key="i"
      >
        <div @click="editproduct(product, i)" class="row">
          <div class="name">
            {{ product.productName }}
            <span v-if="product.amount || product.selectUnit"
              >&nbsp;-&nbsp;</span
            >
            <span class="ml-auto">
              {{ product.amount }}
              {{ product.selectUnit }}
            </span>
          </div>
          <div>
            <button
              v-if="start !== 'true'"
              @click.stop="removeproduct(i, product.productName)"
              class="btn btn-danger"
            >
              удалить
            </button>
            <button
              v-if="start == 'true'"
              @click.stop="removeproduct(i, product.productName)"
              class="btn btn-warning"
            >
              Уже куплен
            </button>
          </div>
        </div>
      </div>
    </div>

    <ProductModal
      v-show="isModalVisible"
      @close="closeModal"
      @show="showModal"
      @save="saveproduct"
      :productInfo="productInfo"
      :isEdit="isEdit"
    />
  </div>
</template>

<script>
export default {
  name: "productsPage",
  components: {
    ProductModal: () => import("./ProductModal.vue"),
  },
  data() {
    return {
      products: [],
      start: false,
      action: false,
      shoppingList: [],
      productInfo: {
        productName: "",
        amount: "",
        selectUnit: "",
      },
      isModalVisible: false,
      isEdit: false,
      editRowKey: "",
    };
  },

  async created() {
    if (localStorage.getItem("shoppingList") !== null) {
      this.products = await JSON.parse(localStorage.getItem("shoppingList"));
      this.shoppingList = await JSON.parse(
        localStorage.getItem("shoppingList")
      );
    }
    if (localStorage.getItem("startShopping") !== null) {
      this.start = localStorage.getItem("startShopping");
    }
    if (localStorage.getItem("action") !== null) {
      this.action = localStorage.getItem("action");
    }
  },

  methods: {
    createNewproduct() {
      this.isEdit = false;
      this.clear();
      this.showModal();
    },
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
      this.isEdit = false;
    },
    saveproduct(info) {
      if (!this.isEdit) {
        this.products.push(info);
        localStorage.setItem("shoppingList", JSON.stringify(this.products));
        this.shoppingList = JSON.parse(localStorage.getItem("shoppingList"));
        this.isModalVisible = false;
      } else {
        this.products[this.editRowKey] = { ...info };
        localStorage.setItem("shoppingList", JSON.stringify(this.products));
        this.shoppingList = JSON.parse(localStorage.getItem("shoppingList"));
        this.isModalVisible = false;
      }
    },
    startShopping() {
      localStorage.setItem("startShopping", JSON.stringify(true));
      this.start = localStorage.getItem("startShopping");
      localStorage.setItem("action", true);
      this.action = localStorage.getItem("action");
    },
    endShopping() {
      localStorage.setItem("startShopping", false);
      this.start = localStorage.getItem("startShopping");
      localStorage.setItem("action", false);
      this.action = localStorage.getItem("action");
      localStorage.clear();
      this.products = [];
      this.shoppingList = [];
    },
    async removeproduct(key, productName) {
      this.products = this.products.filter(
        (item) => item.productName !== productName
      );
      await localStorage.setItem("shoppingList", JSON.stringify(this.products));
      this.shoppingList = await JSON.parse(
        localStorage.getItem("shoppingList")
      );
    },
    editproduct(product, key) {
      this.isEdit = true;
      this.showModal();
      this.editRowKey = key;
      this.productInfo = { ...product };
    },
    clear() {
      for (const prop in this.productInfo) {
        this.productInfo[prop] = "";
      }
    },
  },
};
</script>

