  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 100px;
  @media (max-width: 992px) {
    padding: 0 10px;
    margin: 0px 20px 0px 20px;
  }
  .row {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    overflow: auto;
    width: 100%;
    padding: 10px 0;
    border-bottom: 1px solid;

    &:hover {
      cursor: pointer;
